#root > .App{
  padding: 0;
  background-color: #D3D3D3;
}
.Header{
  border-bottom: 10px solid #00FFFF;
  background-size: cover;
  background-repeat: round;
}
.gradient{
  background-color:rgba(80, 53, 53, 0.5);
  height: 205px;
    display: flex;
    justify-content: center;
}
img, .gradient{
  width: 100%;
}
.Header img, .BrowseArtists img, .BrowseArtists{
  position: relative;
}
.Header h1{
  color: white;
  font-weight: bold;
  margin: 0;
  align-self: center;
}
.BrowseArtists{
  color: white;
  margin: 25px;
  overflow: hidden;
}
.BrowseArtists img{
  height: 250px;
  width: 400px;
  padding: 0px 48px;
  transform: scale(1.5);
  transform-origin: 50% 50%;
}
.BrowseArtists h4{
  position: absolute;
  bottom: 0px;
  left: 0px;
  margin: 0;
  background-color: #2196F3;
  padding: 10px;
  font-size: 20px;
  font-weight: 400;
}
.Browse, .ArtistsPage{
  text-align:center;
  padding-bottom: 15px;
}
.ArtistsPage{
  font-size: 15.1px;
  padding-bottom: 40px;
}
.Browse > h4{
  padding: 20px 0 10px;
  color: black;
  font-weight: 700;
}
.ArtistsPage > img{
  width: 150px;
  margin: 25px;
} 
.Albums > div:nth-child(1), .Albums > div:nth-child(3){
  padding: 0 0 0 50px;
}
.Albums > div:nth-child(2), .Albums > div:nth-child(4){
  padding: 0 50px 0 0;
}
.row{
  margin: 0;
}
a:hover:-webkit-any-link{
  text-decoration: unset;
}
@media (max-width: 426px){
.BrowseArtists img{
  width: 350px;
}
.ArtistsPage p {
  font-size: 13.5px;
}
}